<template>
    <v-container fluid class="container" @click="timer_logout_set()">
        <layout-header/>
        <v-row class="contents" align="center" justify="center">
            <list-menu-list
                v-if="!exerciseEmpty"
                class="content-body"
                title="요가"
                description="운동을 시작하려면 해당 운동을 선택해주세요."
                :thumbnailList="thumbnailList"
            />
            <play-exercise-empty 
                v-else
                :exerciseEmptyItems="exerciseEmptyItems"
            />
        </v-row>
    </v-container>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex'
import exercise from '@/mixins/exercise.js'
import timer_logout from '@/mixins/timer_logout.js'

export default {
    name: 'Yoga',
    mixins:[ exercise, timer_logout ],
    data:()=>({
        thumbnailList: [],
        exerciseEmpty: false,
        exerciseEmptyItems:{
            title:'등록된 운동이 없습니다.',
            description: '현재 기기에 등록된 요가 프로그램이 없습니다.',
            description_1: 'Smart Mirror에 요가 운동을 등록한 뒤 이용해 주세요.'
        }
    }),
    computed:{
        ...mapGetters([
            'GET_MENU_YOGA_LIST',
            'GET_MENU_EX_RESULT_LIST',
        ])
    },
    async mounted() {
        await this.GetExResultList()
        await this.ACT_MENU_API_GET_YOGA_LIST()
        .then(()=>{
            this.EditYogaList()
        })
    },
    methods: {
        ...mapActions([
            'ACT_MENU_API_GET_EX_RESULT_LIST',
            'ACT_MENU_API_GET_YOGA_LIST',
        ]),
        ...mapMutations([
            'MU_MENU_YOGA_LIST',
        ]),
        async GetExResultList(){
            const exType = 0
            await this.ACT_MENU_API_GET_EX_RESULT_LIST(exType)
        },
        async EditYogaList(){
            for( const item of this.GET_MENU_YOGA_LIST){
                item.name       = item.exSortName
                item.link       = 'PartPlay'
                item.tabType    = item.bodyPartCode
                item.exMode     = 1
                item.switchType = 0

                // 운동 이력 체크
                let check = this.GET_MENU_EX_RESULT_LIST.exList.find(list => list.exSortCode === item.exSortCode)
                if(check !== undefined) item.check = true
                
                // 이미지 추가
                item.image  = this.exercise_create_image(item.exSortCode)
                
                // video 추가
                item.video = this.exercise_create_ex_video(item.exSortCode)
                
                if(item.video !== undefined) this.thumbnailList.push(item)
            }
            if(this.thumbnailList.length > 0){
                this.exerciseEmpty = false
            }else{
                this.exerciseEmpty = true
            }
            this.MU_MENU_YOGA_LIST(this.thumbnailList)
            // console.log('this.thumbnailList',this.thumbnailList)
            // console.log('check',this.GET_MENU_EX_RESULT_LIST)
        },
    }
}
</script>

